/*
Base Controls

The example form below demonstrates common HTML form elements that receive updated styles from Bootstrap with additional classes.

Markup:
<div class="form-group">
  <label>Email address</label>
  <input type="email" class="form-control {{modifier_class}}" aria-describedby="emailHelp" placeholder="Enter email">
  <span class="form-text text-muted">We'll never share your email with anyone else.</span>
</div>
<div class="form-group">
  <label for="exampleSelect1">Example select</label>
  <select class="form-control {{modifier_class}}" id="exampleSelect1">
    <option>1</option>
    <option>2</option>
    <option>3</option>
    <option>4</option>
    <option>5</option>
  </select>
</div>
<div class="form-group">
  <label for="exampleSelect2">Example multiple select</label>
  <select multiple="" class="form-control {{modifier_class}}" id="exampleSelect2">
    <option>1</option>
    <option>2</option>
    <option>3</option>
    <option>4</option>
    <option>5</option>
  </select>
</div>
<div class="form-group">
  <label for="exampleTextarea">Example textarea</label>
  <textarea class="form-control {{modifier_class}}" id="exampleTextarea" rows="3" spellcheck="false"></textarea>
</div>

:hover - When user hovers over control.
:focus - When control is focused.
.form-control-sm - A small control.
.form-control-lg - A large control.

Weight: 1

Styleguide Components.forms.base-controls
 */

